import React, { Component } from 'react';
import { connect } from 'react-redux';
import { BootstrapTable, TableHeaderColumn } from 'react-bootstrap-table';
import * as action from '../state/actions/actions';


const TaskTable = (props) => {

  var data = props.data;


  let table = null;
  if (data.length > 0) {
    const tableRows = data.map((taskItem) => {

      return (
        <tr key={taskItem['task-id']} >
          <td>{taskItem['task-id']}</td>
          <td>{taskItem['task-name']}</td>
          <td>{taskItem['task-status']}</td>
          <td>{taskItem['task-container-id']}</td>
          <td>{taskItem['task-proc-inst-id']}</td>
          <td>{taskItem['task-proc-def-id']}</td>
          <td>
            <input
              name="approve"
              type="button"
              className="btn btn-sm btn-outline btn-default"
              onClick={(e, v) => props.handleSelectTask(e, taskItem['task-id'])}
              value="Approve"
              style={{ color: "#1E8B49", border: "2px solid #1E8B49" }}
            />
            <input
              name="reject"
              type="button"
              className="btn btn-sm btn-outline btn-default"
              onClick={(e, v) => props.handleSelectTask(e, taskItem['task-id'])}
              value="Reject"
              style={{ color: "#E84C4D", border: "2px solid #E84C4D" }}
            />
          </td>
        </tr>

      );

    })
    table =
      <table className="table" id="myTable" >
        <thead>
        <td><span style={{ paddingLeft: "2px", fontWeight: "bold" }}> Task # </span></td>
          <td><span style={{ paddingLeft: "45px", fontWeight: "bold" }}> Task Name </span></td>
          <td><span style={{ paddingLeft: "5px", fontWeight: "bold" }}> Status </span></td>
          <td><span style={{ paddingLeft: "70px", fontWeight: "bold" }}>Description</span></td>
          <td><span style={{ paddingLeft: "0px", fontWeight: "bold" }}>Container #</span></td>
          <td><span style={{ paddingLeft: "80px", fontWeight: "bold" }}>Proc Ins #</span></td>
          <td><span style={{ paddingLeft: "50px", fontWeight: "bold" }}>Proc Def #</span></td>
          <td></td>
        </thead>
        <tbody>
          {tableRows}
        </tbody>
      </table>
  }

  return (
    <div>
      {table}
    </div>
  );
};
class MyWorkListApp extends Component {

  componentDidMount() {

    const caseId = localStorage.getItem("caseId");
    const accessToken = localStorage.getItem("accessToken");
    // const accessToken = "5ff412ad58d92050f743e82066c419f1";
    // const caseId = "FR-0000000169";

    if (accessToken) {
      this.props.remoteGetWorkList(accessToken, caseId);
    }
  }

  handleSelectTask = (e, taskId) => {

    const caseId = localStorage.getItem("caseId");
    const accessToken = localStorage.getItem("accessToken");
    // const accessToken = "5ff412ad58d92050f743e82066c419f1";
    const targetName = e.target.name;
    this.props.selectTask(taskId);

    const loading = this.props.loading;
    const that = this;

    if (targetName === 'approve') {
      Promise.resolve(
        this.props.remoteApproveTask(accessToken, taskId)
      )
        .then(function () {
          if (!loading) {
            that.props.remoteGetWorkList(accessToken, caseId);
            window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-backoffice/en/admin_task_detail.page';
          }
        })


    } else if (targetName === 'reject') {
      Promise.resolve(
        this.props.remoteRejectTask(accessToken, taskId)
      )
        .then(function () {
          if (!loading) {
            that.props.remoteGetWorkList(accessToken, caseId);
            window.location = 'http://' + window.location.host + '/fsi-credit-card-dispute-backoffice/en/admin_task_detail.page';
          }
        })

    }
  }

  render() {
    const workList = this.props.workList;
    var tabel = null;

    if (workList != null) {
      tabel = <TaskTable
        data={workList}
        handleSelectTask={(e, taskId) => this.handleSelectTask(e, taskId)}
      />;
    }

    return (
      <div className="ibox float-e-margins">
        <div className="ibox-title" style={{lineHeight:'33px', textAlign:'left'}} >
          <h2 style={{fontSize:'24px', fontStyle:'Light'}}><a>ENGAGE </a>| <a>TASKS</a></h2>
        </div>
        <div className="ibox-content" id="main-contents" style={{ fontFamily: "Open Sans" }}>
          <div className="row">

            <div className="col-sm-10" style={{ marginLeft: "-20px", marginTop: "-15px", lineHeight:'22px'}}>
              <button type="button" style={{ float: "left", backgroundColor: '#35CAED', border: 'none', color: 'white', height: '62px', width: '101.54px', fontSize: '16px' }}>All</button>
              <button type="button" style={{ float: "left", backgroundColor: '#FFFFFF', border: 'none', color: 'black', height: '62px', width: '101.54px', fontSize: '16px' }}>Overdue</button>
            </div>

            <div className="col-sm-2" style={{ marginLeft: "-20px", marginTop: "-15px", lineHeight:'18px'}}>
  
                  <div className="search-container" style={{ paddingTop:'10.99px', display: "inline-block" }}>
                    <form>
                      <input type="text" placeholder="Search" name="search" style={{ width: '172.91px', height:'30px',border: 'true' }} />
                      <button type="button" style={{ float: "right", marginLeft: '-2px', backgroundColor: '#005a91', color: 'white', width:'34.41',height: '30px', fontSize:'12px' }}>Go!</button>
                    </form>
                  </div>
                
            </div>



          </div>
          <br /><br /><br />
          {tabel}

        </div>
      </div>
    );
  }
}

const mapStateToProps = state => ({
  workList: state.workFlowReducer.workList,
  selectedTask: state.workFlowReducer.selectedTask,
  selectedCase: state.workFlowReducer.selectedCase,
  loading: state.workFlowReducer.loading,
});
const mapDispatchToProps = dispatch => ({
  remoteGetWorkList: (accessToken, caseId) => dispatch(action.remoteGetWorkList(accessToken, caseId)),
  selectTask: (taskid) => dispatch(action.selectTask(taskid)),
  selectCase: (caseId) => dispatch(action.selectCase(caseId)),
  remoteApproveTask: (accessToken, taskId) => dispatch(action.remoteApproveTask(accessToken, taskId)),
  remoteRejectTask: (accessToken, taskId) => dispatch(action.remoteRejectTask(accessToken, taskId)),
});
export default connect(mapStateToProps, mapDispatchToProps)(MyWorkListApp);


