import request from "../../../node_modules/superagent/superagent";
import { reformatWorkList } from '../../shared/utility';

export const GET_WORK_LIST = 'GET_WORK_LIST';
export const GET_TASK_DETAILS = 'GET_TASK_DETAILS';
export const SELECTED_TASK = 'SELECTED_TASK';

export const GET_CASE_LIST = 'GET_CASE_LIST';
export const SELECTED_CASE = 'SELECTED_CASE';

export const REST_CALL_START = 'REST_CALL_START';
export const REST_CALL_SUCCESS = 'REST_CALL_SUCCESS';
export const REST_CALL_FAIL = 'REST_CALL_FAIL';

// // //ENTANDO DEPLOYMENT UNCOMMENT THIS
const GET_WORK_LIST_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/caseTasks/";
const GET_TASK_DETAILS_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/taskdetail/";
const COMPLETED_TASK_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/completetask/credit-dispute-case_1.0-SNAPSHOT/";
const GET_CASE_LIST_ENDPOINT = "http://" + window.location.host + "/fsi-credit-card-dispute-backoffice/api/kiebpm/credit-dispute-case_1.0-SNAPSHOT/cases/instances"

// //NPM START UNCOMMENT THIS
// const GET_WORK_LIST_ENDPOINT = "http://localhost:8181/fsi-credit-card-dispute-backoffice/api/kiebpm/caseTasks/";
// const GET_TASK_DETAILS_ENDPOINT = "http://localhost:8181/fsi-credit-card-dispute-backoffice/api/kiebpm/taskdetail/";
// const COMPLETED_TASK_ENDPOINT = "http://localhost:8181/fsi-credit-card-dispute-backoffice/api/kiebpm/completetask/credit-dispute-case_1.0-SNAPSHOT/";
// const GET_CASE_LIST_ENDPOINT = "http://localhost:8181/fsi-credit-card-dispute-backoffice/api/kiebpm/credit-dispute-case_1.0-SNAPSHOT/cases/instances"

export const getWorkList = (payload) => {
  return {
    type: GET_WORK_LIST,
    payload: payload,
  };
}
export const getTaskDetails = (payload) => {
  return {
    type: GET_TASK_DETAILS,
    payload: payload,
  };
}

export const getCaseList = (payload) => {
  return {
    type: GET_CASE_LIST,
    payload: payload,
  };
}
export const selectTask = (taskid) => {
  return {
    type: SELECTED_TASK,
    payload: taskid,
  };
}
export const selectCase = (caseId) => {
  return {
    type: SELECTED_CASE,
    payload: caseId,
  };
}
export const restCallSuccess = () => {
  return {
    type: REST_CALL_SUCCESS
  };
};
export const restCallStart = () => {
  return {
    type: REST_CALL_START
  };
};

export const restCallFail = (error) => {
  return {
    type: REST_CALL_FAIL,
    error: error
  };
};

export const remoteGetWorkList = (accessToken, caseId) => {
  return dispatch => {
    dispatch(restCallStart());

    console.log(accessToken);
    console.log(caseId);
    var payloadArray = [];
    request
      .get(GET_WORK_LIST_ENDPOINT + caseId)
      .set('Content-Type', 'application/json')
      .set('Accept', 'application/json')
      .set('Authorization', 'Bearer ' + accessToken)
      .query({ page: 0,  pageSize: 1000})
      .then((res) => {
        dispatch(restCallSuccess());
        payloadArray = res.text;
        const resJS = JSON.parse(payloadArray);
        const taskSummary = resJS['task-summary'];

        console.log("Server Response " + JSON.stringify(resJS));
        dispatch(getWorkList(taskSummary));
      })
      .catch((err) => {
        var tmp = {};

        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        console.log("Server error " + JSON.stringify(tmp.errorMessage));
        console.log("Server error " + JSON.stringify(tmp.errorResponse));
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}
export const remoteGetCaseList = (accessToken) => {
  return dispatch => {
    dispatch(restCallStart());

    // console.log("Action " + constant.GET_NOTIFICATION_HISTORY);
    request
      .get(GET_CASE_LIST_ENDPOINT)
      .set('Content-Type', 'application/json')
      .set('Accept', 'application/json')
      .set('Authorization', 'Bearer ' + accessToken)
      .then((res) => {
        dispatch(restCallSuccess());

        const response = res.body;
        // console.log("outputs " + JSON.stringify(output));
        const instances = response.instances;
        // console.log("Server Response " + JSON.stringify(instances));

        return dispatch(getCaseList(instances));
      })
      .catch((err) => {
        var tmp = {};
        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        console.log(tmp);
        console.log(err);
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}
export const remoteGetTaskDetails = (taskId) => {
  return dispatch => {
    dispatch(restCallStart());

    var payloadArray = {};
    request
      .get(GET_TASK_DETAILS_ENDPOINT + taskId)
      .set('Content-Type', 'application/json')
      .set('Accept', 'application/json')
      .then((res) => {
        dispatch(restCallSuccess());
        var tmp = {};
        tmp.status = res.status;
        tmp.body = res.body;
        console.log(tmp.body);
        payloadArray = tmp.body;
        console.log(payloadArray);
      })
      .catch((err) => {
        var tmp = {};

        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}

export const remoteApproveTask = (accessToken, taskId) => {
  const APPROVE_PAYLOAD =
  {
    "approved_": true
  };
  return dispatch => {
    dispatch(restCallStart());

    request
      .post(COMPLETED_TASK_ENDPOINT + taskId)
      .set('Content-Type', 'application/json')
      .set('Accept', 'application/json')
      .set('Authorization', 'Bearer ' + accessToken)
      .query({ page: 0,  pageSize: 1000})
      .send(APPROVE_PAYLOAD)
      .then((res) => {
        dispatch(restCallSuccess());
        var tmp = {};
        tmp.status = res.status;
        tmp.body = res.body;
        console.log(tmp.body);
      })
      .catch((err) => {
        var tmp = {};

        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}
export const remoteRejectTask = (accessToken, taskId) => {
  const APPROVE_PAYLOAD =
  {
    "approved_": false
  };
  return dispatch => {
    dispatch(restCallStart());

    request
      .post(COMPLETED_TASK_ENDPOINT + taskId)
      .set('Content-Type', 'application/json')
      .set('Accept', 'application/json')
      .set('Authorization', 'Bearer ' + accessToken)
      .query({ page: 0,  pageSize: 1000})
      .send(APPROVE_PAYLOAD)
      .then((res) => {
        dispatch(restCallSuccess());
        var tmp = {};
        tmp.status = res.status;
        tmp.body = res.body;
        console.log(tmp.body);
      })
      .catch((err) => {
        var tmp = {};

        tmp.errorMessage = err.message;
        tmp.errorResponse = err.response;
        dispatch(restCallFail(tmp.errorResponse));
      });
  }
}



