import React from 'react';
import ReactDOM from 'react-dom';
import registerServiceWorker from './registerServiceWorker';

import { createStore, applyMiddleware, compose, combineReducers } from 'redux';
import { Provider } from 'react-redux';
import thunkMiddleware from 'redux-thunk'

import MyWorkListApp from './components/MyWorkListApp';
import workFlowReducer from './state/reducers/reducers';

import './styles/style.css';

// import 'bootstrap/dist/css/bootstrap.css';
// import './../node_modules/bootstrap/dist/css/bootstrap.min.css';
// import './../node_modules/font-awesome/css/font-awesome.css';
// import './../node_modules/animate.css/animate.min.css';
// import './steps/css/main.css';
// import './css/cssoverride.css';
// import './css/yilicss.css';


const composeEnhancers = process.env.NODE_ENV === 'development' ? window.__REDUX_DEVTOOLS_EXTENSION_COMPOSE__ : null || compose;

const rootReducer = combineReducers({
  workFlowReducer: workFlowReducer,
});

const store = createStore(rootReducer, composeEnhancers(
    applyMiddleware(thunkMiddleware)
));

ReactDOM.render(
    <Provider store={store} >
        <MyWorkListApp />
    </Provider>
    , document.getElementById('fsi-ccd-admin-js-case-tasks'));
registerServiceWorker();



